<template>        
    <div class="body">
        <div class="display">
            <div class="background">
                <div>
					<h2> {{num ? '':''}}</h2> <!-- num     -->

					<div class="AddWrap">
						<form>
							<table class="tbAdd">
								<colgroup>
									<col width="15%" />
									<col width="*" />
								</colgroup>
								<tr>
									<th></th>
									<td>
									<!-- v-model     -->
									<input type="text" v-model="subject" ref="subject" /></td>
								</tr>
								<tr>
									<th></th>
									<td><textarea v-model="cont" ref="cont"></textarea></td>
								</tr>
								<tr>
									<th></th>
									<td><input id="password" name="password" type="password" ref="password" placeholder="" v-model="password"/> </td>
								</tr>
							</table>
						</form>
					</div>

					<div class="btnWrap">
						<a href="javascript:;" @click="fnList" class="btn"></a>
						<a v-if="!num" href="javascript:;" @click="fnAddProc" class="btnAdd btn"></a>
					</div>	
				</div>
            </div>
        </div>
        <div class="display-footer">
            <div class="background-footer">
                <p class="contact">CONTACT<br>
                   1,  <br>
                 : 043-261-2480<br>
                TEL : 010-7647-0877 | 010-7935-2882<br>
                 : , <br>
                Copyright 2021 movie project (c) Allrights Reserved
                </p>
            </div>                
        </div> 
    </div>
</template>

<script>
export default {
	data() {
		return{
			board_code:'news'
			,subject:''
			,cont:''
			,password:''
			,id: ''
			,body:this.$route.query 
			,form:'' //form  
			,num:this.$route.query.num
		}
	}
	,mounted() { //   
		if(this.num) { // num     .
			this.fnGetView();
		}
	}
	,methods:{
		fnList(){
			delete this.body.num;
			this.$router.push({path:'./list',query:this.body});
			
		}
		,fnGetView() {
			this.$http.get('/api/view'+this.body.num,{params:this.body})
			.then((res)=>{
				this.view = res.data.view[0];
				this.subject = this.view.subject;
				this.cont = this.view.cont;
				this.password = this.view.password;
			})
			.catch((err)=>{
				console.log(err);
			})
		}
		,fnView() {
			this.$router.push({path:'./view',"query":this.body});
		}	
		,fnAddProc() {
			if(!this.subject) {
				alert("  ");
				this.$refs.subject.focus(); //  .
				return;
			}

			this.form = {
				board_code:this.board_code
				,subject:this.subject
				,cont:this.cont
				,id:this.id
				,password:this.password
			} 
			
			this.$http.post('/api/write',this.form)
			.then((res)=>{
				if(res.data.success) {
					alert('.');
					this.fnList();
				} else {
					alert(" .\n  ");
				}
			})
			.catch((err)=>{
				console.log(err);
			})
			
		}
		,fnModProc() {
			if(!this.subject) {
				alert("  ");
				this.$refs.subject.focus(); //  .
				return;
			}

			this.form = {
				board_code:this.board_code
				,subject:this.subject
				,cont:this.cont
				,id:this.id
				,num:this.num
				,password:this.password
			} 
			
			this.$http.put('/api/mod',this.form)
			.then((res)=>{
				if(res.data.success) {
					alert('.');
					this.fnView();
				} else {
					alert(" .\n  ");
				}
			})
			.catch((err)=>{
				console.log(err);
			})
		}
	}	
}
</script>

<style scoped>
* {
    color : #222;
}
h2{color:#222;}
table{width:200%; margin-left: -50%; border-collapse:collapse;}
.wrap{width:100%;}
.container{width:800px; margin:0 auto;}
a{text-decoration:none;}
.btn{padding:10px; background:rgb(190, 59, 49); color:#fff;}
.background {
  
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
    width: 75%;
    padding: 3% 4%;
    flex-direction: column; 
    /*  */
    border: 1px solid rgb(255, 255, 255);
    border-radius: 2ex;
    overflow: hidden;
}

.display {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75%;
    padding : 1% 12%;
    
}
.tbAdd{border-top:1px solid #888;}
.tbAdd th, .tbAdd td{border-bottom:1px solid #eee; padding:5px 0;}
.tbAdd td{padding:10px 10px; box-sizing:border-box;}
.tbAdd td input{width:100%; min-height:30px; box-sizing:border-box; padding:0 10px;}
.tbAdd td textarea{width:100%; min-height:300px; padding:10px; box-sizing:border-box;}
.btnWrap{text-align:center; margin:20px 0 0 0;}
.btnWrap a{margin:0 10px;}
.btnAdd {background:rgb(190, 59, 49);}
.btnDelete{background:rgb(190, 59, 49);}
</style>