<script>
export default {
    data : function() {
        return {
        check : {
            check1 : false,
            check2 : false,
            check3 : false,
            check4 : false,
            check5 : false,
            }
        }
    },
    computed : {
        checkAll : {
            get: function(e) {
                return this.check1 + ',' + this.check2 + '+' + this.check3 + '+' + this.check4 + '+' + this.check5
            },
            set : function(e) {
                if(e===true) {
                    this.check.check1 = true
                    this.check.check2 = true
                    this.check.check3 = true
                    this.check.check4 = true
                    this.check.check5 = true
                } else {
                    this.check.check1 = false
                    this.check.check2 = false
                    this.check.check3 = false
                    this.check.check4 = false
                    this.check.check5 = false
                }
            }
        }
    },
    methods : {
        submitForm() { 
            if(this.check.check1==false & this.check.check2==false) {
                alert(' ');
            } else {
                this.$router.push('/signUp');
            }
        }
    }
}
</script>
